<template>
  <component :is="mediaLayout">
    <router-view />
  </component>
</template>

<script>
import { computed, defineAsyncComponent, onBeforeUnmount } from 'vue'
import { useStore } from 'vuex'
import { useBreakpointsMedia } from '@/plugins/breakpoints'
import exchangeModule from '@/store/modules/exchange'
import { MODULES_NAMES } from '@/common/modules'

export default {
  components: {
    MobileLayout: defineAsyncComponent(() => import('@/layouts/DefaultLayout/MobileLayout')),
    DesktopLayout: defineAsyncComponent(() => import('@/layouts/DefaultLayout/DesktopLayout'))
  },
  setup () {
    const store = useStore()
    const breakpoints = useBreakpointsMedia()
    const layouts = {
      mobile: 'MobileLayout',
      desktop: 'DesktopLayout'
    }
    const mediaLayout = computed(() => {
      return breakpoints.mobile.matches ? layouts.mobile : layouts.desktop
    })

    if (!store.hasModule(MODULES_NAMES.EXCHANGE)) {
      store.registerModule(MODULES_NAMES.EXCHANGE, exchangeModule)
    }
    onBeforeUnmount(() => {
      if (store.hasModule(MODULES_NAMES.EXCHANGE)) {
        store.unregisterModule(MODULES_NAMES.EXCHANGE)
      }
    })
    return {
      mediaLayout
    }
  }
}
</script>
