import coinsMock from '@/store/mock/coinsMock'

export default {
  namespaced: true,
  state: {
    coinsList: coinsMock,
    sendCoin: coinsMock[0],
    receiveCoin: coinsMock[1],
    email: 'notifyme@myemail.com'
  },

  mutations: {
    SET_EMAIL (state, payload) {
      state.email = payload
    },
    SET_SEND_COIN (state, payload) {
      state.sendCoin = payload
    },
    SET_RECEIVE_COIN (state, payload) {
      state.receiveCoin = payload
    },
    SET_SEND_COIN_AMOUNT (state, payload) {
      state.sendCoin = {
        ...state.sendCoin,
        amount: payload
      }
    },
    SET_SEND_RECEIVE_AMOUNT (state, payload) {
      state.receiveCoin = {
        ...state.receiveCoin,
        amount: payload
      }
    }
  },

  actions: {
    setSendCoin ({ commit }, payload) {
      commit('SET_SEND_COIN', payload)
    },
    setReceiveCoin ({ commit }, payload) {
      commit('SET_RECEIVE_COIN', payload)
    },
    setEmail ({ commit }, payload) {
      commit('SET_EMAIL', payload)
    }
  },

  getters: {
    getSendCoin (state) {
      return state.sendCoin
    },
    getReceiveCoin (state) {
      return state.receiveCoin
    },
    getEmail (state) {
      return state.email
    },
    getCoinsList (state) {
      return state.coinsList
    }
  }
}
